html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
 
  font-family: 'Orbitron', sans-serif !important;
}
span {
  display: block;
}

h1 {
  margin-top: 5vw;
  text-align: center;
  font-size: 3.7vw;
}
 .input-wrap {
  width: 31%;

  margin: 5vw auto;
 }

#datetime-picker {
  font-family: inherit;
 
  margin-right: 20px;
  font-size: 2vw;
  width: 22vw;
  /* height: 5vh; */
  border-radius: 10px;

}
#datetime-picker:disabled {
  opacity: 0.4;
}

  button[type="button"] {
    font-family: inherit;
    /* height: 5vh; */
    font-size: 2vw;
    text-align: center;
    color: #fff;    background-color: #000;
    border-radius: 10px;
    box-shadow: 5px 5px 5px #adafb2;
  } 
  button[type="button"]:disabled {
    opacity: 0.4;
    box-shadow: none;
  }
.timer {
  display: flex;
  justify-content: space-between;
  padding: 1%;
  
  width: 45%;
  margin: 0 auto;
  color: #fff;
  border: 1px solid #adafb2;
  border-width: 1px 0;
  overflow: hidden;
}

.field {
  display: inline-block;
  width: 10vw;
  /* font-size: 72px; */
  font-size: 5vw;
  text-align: center;
  background-color: #000;
  border-radius: 10px;
  box-shadow: 5px 5px 5px #adafb2;
}
.value {
  font-family: 'Orbitron', sans-serif;
  display: block;
}
.label {
  color: #adafb2;
  font-size: 18px;
  font-size: 1.5vw;
  text-transform: uppercase;
}




